"use client";
import { useState } from "react";
import { FileText, MessageSquare, CreditCard, Settings } from "lucide-react";

type Tab = "facturador" | "plantillas" | "metodos" | "sistema";

export default function BarraNavegacion() {
  const [tabActiva, setTabActiva] = useState<Tab>("plantillas");

  return (
    <div className="flex justify-center bg-[#f5f6f7] py-4">
      <div className="flex gap-2 bg-[#f1f1f1] p-1 rounded-xl">
        <PillTab
          icon={<FileText size={16} />}
          label="Facturador"
          active={tabActiva === "facturador"}
          onClick={() => setTabActiva("facturador")}
        />
        <PillTab
          icon={<MessageSquare size={16} />}
          label="Plantillas"
          active={tabActiva === "plantillas"}
          onClick={() => setTabActiva("plantillas")}
        />
        <PillTab
          icon={<CreditCard size={16} />}
          label="MÃ©todos de Pago"
          active={tabActiva === "metodos"}
          onClick={() => setTabActiva("metodos")}
        />
        <PillTab
          icon={<Settings size={16} />}
          label="Sistema"
          active={tabActiva === "sistema"}
          onClick={() => setTabActiva("sistema")}
        />
      </div>
    </div>
  );
}

function PillTab({
  icon,
  label,
  active,
  onClick,
}: {
  icon: JSX.Element;
  label: string;
  active: boolean;
  onClick: () => void;
}) {
  return (
    <button
      onClick={onClick}
      className={`flex items-center gap-2 px-4 py-2 text-sm font-medium rounded-lg transition-all duration-150
        ${active ? "bg-white shadow text-black" : "text-gray-600 hover:bg-gray-200"}`}
    >
      {icon}
      {label}
    </button>
  );
}
